.accordion__revealer {
  display: none;
}

@media screen and (min-width: 1201px) {
  .accordion__revealer {
    display: block !important;
  }
}

.accordion__btn {
  text-decoration: none !important;
}

@media screen and (min-width: 1201px) {
  .accordion__btn {
    display: none;
  }
}

.accordion__icon {
  float: left;
  width: 20px;
  position: relative;
  margin: 7px auto;
  display: block;
  margin-right: 10px;
}

.accordion__icon, .accordion__icon:before, .accordion__icon:after {
  height: 5px;
  background-color: #000;
  transition: all 0.2s ease-in-out;
  border-radius: 5px;
}

.accordion__icon:before, .accordion__icon:after {
  content: "";
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
}

.accordion__icon:before {
  bottom: 7px;
}

.accordion__icon:after {
  top: 7px;
}

.-accordion--open .accordion__icon {
  background-color: transparent;
}

.-accordion--open .accordion__icon:before {
  transform: rotate(45deg);
  bottom: 0;
  margin: 0;
}

.-accordion--open .accordion__icon:after {
  transform: rotate(-45deg);
  top: 0;
  margin: 0;
}

