.breadcrumb {
  margin-top: 10px;
}

.breadcrumb__item {
  display: inline-block;
  vertical-align: middle;
  padding-right: 15px;
  margin-right: 5px;
  position: relative;
  color: #999999;
}

.breadcrumb__item:before {
  border: 1px solid #999999;
  border-width: 0 1px 1px 0;
  transition: all 0.2s ease-in-out;
  transform-origin: right bottom;
  height: 5px;
  width: 5px;
  transform: rotate(-45deg);
  position: absolute;
  right: 0.5px;
  bottom: 1.5px;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0;
  content: '';
  display: block;
  right: 0;
  top: -6px;
}

.breadcrumb__item:last-child {
  padding-right: 0;
  margin-right: 0;
}

.breadcrumb__item:last-child:before {
  display: none;
}

.breadcrumb__icon {
  font-size: 20px;
  line-height: 16px;
